document.addEventListener('DOMContentLoaded', () => {
  const allCheckboxFilters = document.querySelectorAll('.checkbox_filter');
  const buttonIsAll = document.querySelector('.checkbox_filter.is-all');

  if (!buttonIsAll || allCheckboxFilters.length === 0) return;

  const updateButtonTransparency = () => {
    const hasActiveFilter = Array.from(allCheckboxFilters).some(filter => 
      !filter.classList.contains('is-all') && 
      filter.classList.contains('fs-cmsfilter_active')
    );

    if (hasActiveFilter) {
      buttonIsAll.style.backgroundColor = 'transparent';
    } else {
      buttonIsAll.style.backgroundColor = '';
    }
  };

  const observer = new MutationObserver(updateButtonTransparency);

  allCheckboxFilters.forEach(filter => {
    observer.observe(filter, {
      attributes: true,
      attributeFilter: ['class']
    });
  });

  updateButtonTransparency();
});
 